.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.side-btn{
  padding-left: 20px;
padding-block:20px ;
display: flex;
background-color: rgb(15, 198, 208);
gap:10px;
align-items: center;
}
.side-btn>img{
  width: 20%;
  height: 20%;
  border-radius: 100%;
}
.side-btn:hover{
  background-color: #528bff;
  cursor: pointer;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
